.text-center {
  text-align: center;
}

//btns
.btn {
  background: none;
  border: none;
  display: inline-block;
  background-color: $primary-color;
  color: #fff;
  font-weight: 700;
  font-size: 2.2rem;
  border: 1px solid transparent;
  cursor: pointer;
  padding: 1.6rem 6.5rem;

  &:hover {
    background-color: #fff;
    border: 1px solid $primary-color;
    transition: all .3s;
    color: $primary-color;
  }

}


//Slider buttons
.slider-arrow {
  display: flex;
  margin-top: 7rem;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  right: 1rem;
  cursor: pointer;

  @include respond(tab-port) {
    margin-top: 3rem;
    margin-bottom: 2rem;
  }


  &--left {
    transform: rotate(-180deg);
    height: 2rem;
    width: 4rem;

    path {
      fill: rgba(#fff, .7);
    }

  }

  &--right {
    width: 6rem;
    height: 3rem;
    margin-left: 2rem;

    path {
      fill: #fff;
    }

  }

}


//LAYOUT GRID

.layout-container {
  width: 100%;
}

.main {
  width: 100%;
}


.grid {
  display: flex;
  justify-content: space-between;
  @include respond(tab-land) {
    flex-direction: column;
  }

  .col-1 {
    width: 15%;

    @include respond(desktop) {
      width: 10%;
    }

    @include respond(tab-land) {
      display: none;
    }

  }

  .col-2 {
    width: 36%;

    @include respond(desktop) {
      width: 40%;
    }
    
    @include respond(tab-land) {
      width: 100%;
    }


  }

  .col-3 {
    width: 8%;

    @include respond(desktop) {
      width: 10%;
    }

  }
  
  .col-4 {
    width: 26%;

    @include respond(desktop) {
      width: 30%;
    }
    @include respond(tab-land) {
      width: 100%;
    }
  }

  .col-5 {
    width: 15%;

    @include respond(desktop) {
      width: 10%;
    }

    @include respond(tab-land) {
      display: none;
    }
  }

  

  

  @include col-x {
    border-right: 1px solid rgba(0,0,0, .06);
    border-bottom: 1px solid rgba(0,0,0, .06);

    @include respond(tab-land) {
      // width: 100%;
      border-right: none;
    }

  }

  
  .col-5 {
    border-right: none;
  }

}

